* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapper-fluid {
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;
    padding-right: 10px;
    padding-left: 10px;
}

.wrapper {
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;
    padding-right: 0px;
    padding-left: 0px;
}

.centering {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

._100 {
    width: 100%;
}

._50 {
    width: 50%;
}

.img-fw {
    width: 100%;
}

.img-ha {
    height: auto;
}

.f-right {
    float: right;
}

.f-left {
    float: left;
}

.f-none {
    float: none;
}

.t-light {
    color: white;
}

.green {
    background-color: green;
}

.red {
    background-color: red;
}

.t-right {
    text-align: right;
}

.t-left {
    text-align: left;
}

.t-center {
    text-align: center;
}

.clearfix {
    clear: both;
    content: "";
}

.heading-primary {
    padding-top: 3em;
    padding-bottom: 3em;
}

.list-none {
    list-style: none;
}

.t-weight-100 {
    font-weight: 100;
}

.t-weight-300 {
    font-weight: 300;
}

.t-weight-600 {
    font-weight: 700;
}

.t-weight-900 {
    font-weight: 900;
}

.saver {
    position: relative;
    z-index: -1;
    display: table;
    margin: 0;
    max-width: none;
}

[class*="col-fl"] {
    width: 100%;
    float: left;
    padding: 15px;
    /* border: 1px solid black; */
}

[class*="col-fr"] {
    width: 100%;
    float: right;
    padding: 15px;
    /* border: 1px solid black; */
}


/* tablet */

@media (min-width: 600px) {
    .col-fl-md-1 {
        width: 8.33%;
    }
    .col-fl-md-2 {
        width: 16.66%;
    }
    .col-fl-md-3 {
        width: 25%;
    }
    .col-fl-md-4 {
        width: 33.33%;
    }
    .col-fl-md-5 {
        width: 41.66%;
    }
    .col-fl-md-6 {
        width: 50%;
    }
    .col-fl-md-7 {
        width: 58.33%;
    }
    .col-fl-md-8 {
        width: 66.66%;
    }
    .col-fl-md-9 {
        width: 75%;
    }
    .col-fl-md-10 {
        width: 83.33%;
    }
    .col-fl-md-11 {
        width: 91.66%;
    }
    .col-fl-md-12 {
        width: 100%;
    }
    /* float right */
    .col-fr-md-1 {
        width: 8.33%;
    }
    .col-fr-md-2 {
        width: 16.66%;
    }
    .col-fr-md-3 {
        width: 25%;
    }
    .col-fr-md-4 {
        width: 33.33%;
    }
    .col-fr-md-5 {
        width: 41.66%;
    }
    .col-fr-md-6 {
        width: 50%;
    }
    .col-fr-md-7 {
        width: 58.33%;
    }
    .col-fr-md-8 {
        width: 66.66%;
    }
    .col-fr-md-9 {
        width: 75%;
    }
    .col-fr-md-10 {
        width: 83.33%;
    }
    .col-fr-md-11 {
        width: 91.66%;
    }
    .col-fr-md-12 {
        width: 100%;
    }
}


/* desktop */

@media only screen and (min-width: 768px) {
    .col-1 {
        width: 8.33%;
    }
    .col-2 {
        width: 16.66%;
    }
    .col-3 {
        width: 25%;
    }
    .col-4 {
        width: 33.33%;
    }
    .col-5 {
        width: 41.66%;
    }
    .col-6 {
        width: 50%;
    }
    .col-7 {
        width: 58.33%;
    }
    .col-8 {
        width: 66.66%;
    }
    .col-9 {
        width: 75%;
    }
    .col-10 {
        width: 83.33%;
    }
    .col-11 {
        width: 91.66%;
    }
    .col-12 {
        width: 100%;
    }
}
body {
  background: -webkit-gradient(linear, left top, left bottom, from(#b3d3e5), to(#3994cf));
  background: linear-gradient(#b3d3e5, #3994cf);
  font-family: "Roboto", sans-serif;
  height: 100vh;
  z-index: -1;
  position: relative;
}

.wholeWeather {
  border-style: none;
  margin: 4em;
  position: relative;
  z-index: -1;
  display: table;
  margin: 0;
  max-width: none;
  width: 100%;
  /* background-color: #9ed8f4; */
}

.pre {
  z-index: -1;
  position: relative;
}

.singleWeather {
  border-style: none;
  height: 82vh;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#376487), to(#718691));
  background: linear-gradient(#376487, #718691);
  position: relative;
  right: 1em;
  color: #abafb3;
  box-shadow: 13px 6px 27px -9px #848282;
}

.image {
  height: 56vh;
}

.text {
  color: azure;
  position: absolute;
  bottom: 4em;
  left: 6em;
}

.singleWeatherImg {
  border-style: none;
  background-image: url("https://images.pexels.com/photos/3027216/pexels-photo-3027216.jpeg?cs=srgb&dl=photo-of-multi-coloured-hot-air-balloons-3027216.jpg&fm=jpg");
  width: 100%;
  height: 82vh;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  left: 1em;
  box-shadow: 15px -5px 20px -7px #848282;
}

.singleWeatherImg img {
  width: 100%;
  height: 82vh;
}

hr {
  width: 71%;
  border-color: #d2cbcb4a;
}

.search {
}

.search form {
}

.search form input {
}

.countriesList {
  position: relative;
  left: 1em;
  top: 8em;
}

.weatherListDetails {
  position: relative;
  left: 1em;
  top: 13em;
}

.countriesList li {
  list-style: none;
  padding-bottom: 10px;
}

.weatherListDetails li {
  list-style: none;
  padding-bottom: 10px;
}

.countriesList li:nth-child(1) {
  list-style: none;
  padding-top: 10px;
}

.weatherListDetails li:nth-child(1) {
  list-style: none;
  padding-top: 10px;
}

.degree {
  font-size: 72px;
}

.city {
  font-size: 45px;
}

.city > span {
  font-size: 15px;
  position: relative;
  right: -134px;
  top: -36px;
}

.text span img {
  width: 35px;
  height: 35px;
  position: relative;
  right: -10em;
  top: -3em;
  border-style: hidden;
  border-radius: 16px;
}

